import axios from 'axios';
import { authHeader } from './authservice/auth-header';

const API_URL = process.env.VUE_APP_BASE_API_URL;

class BannerService {
    getBanner() {
        return axios.get(API_URL+'banner', { headers: authHeader() });
    }
    addBanner(data){
        return axios.post(API_URL+'banner', data, { headers: authHeader() });
    }
    updateBanner(id, data) {
        return axios.post(API_URL+'banner/update/'+id, data, { headers: authHeader() });
    }
    deleteBanner(id) {
        return axios.delete(API_URL+'banner/'+id, { headers: authHeader() });
    }
 
        
}

export default new BannerService();
